<script>
import axios from "axios";
import fillbtn from "../components/fillbtn";
import Header from "../components/Header";
import Sidemenu from "../components/Sidemenu";
import Footer from "../components/footer";
import alertblock from "../components/Alertblock";
export default {
  data() {
    return {
      eventinfo: [],
      loadingcircle: true,
      hassignup: false,
      usereventinfo: [],
      isdeadline: false,
      olderangelinfo:[]
      // maincolor: "#9b1e32"
    };
  },
  created() {
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbzW4IbkVHdr_DCY3U65uBx4zV9cCmSBeU-FT1QsK_UbloDhniYPT-TGXQi5tky9M5UR/exec",
      params: {
        mode: "query",
        id: this.$route.params.id
      }
    }).then(response => {
      this.eventinfo = response.data[0];
      this.loadingcircle = false;
      var today = new Date();
      let nowdays = new Date(
        today.getFullYear() +
          "/" +
          (today.getMonth() + 1) +
          "/" +
          today.getDate()
      );
      let actdate = new Date(this.eventinfo[8]);
      let minusdays = parseInt((actdate - nowdays) / 1000 / 60 / 60 / 24);
      // console.log(minusdays, this.eventinfo[8]);
      if (minusdays <= 0) {
        this.isdeadline = true;
      }
      this.eventinfo[2] = this.convertdate(this.eventinfo[2]);
      this.eventinfo[8] = this.convertdate(this.eventinfo[8]);

      this.$store.dispatch("gethostinfo", this.eventinfo[17]);
      this.$store.commit("sethostid", this.eventinfo[17]);
      console.log(response.data);
    });
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbxqMsajmkgXY6oxaJlCh3tRP5Rr8MEh0GpBSkcR7amFy7cL_YorIGZvftoQnSv6Nn7R8Q/exec",
      params: {
        mode: "user",
        //在id地方帶入活動的id並用get方法取得此檔活動的所有報名者資訊
        id: this.$route.params.id
      }
    })
      .then(response => {
        this.usereventinfo = response.data;

        // this.emitarr[0] = this.userdata.displayName;

        //這邊做一個判別判斷此報名者是否報名過了
        for (let i = 0; i < response.data.length; i++) {
          if (response.data[i][0] == this.userdata.displayName) {
            this.hassignup = true;
          }
        }
      })
      .catch(error => {
        console.error(error);
      });
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbxGkIBwYN29d0h-Pf-lTSKe_BvRCt-Eta3pDYdT-A_Us6n3L5QLFmfXYIL2Hja0d5uj/exec",
      params: {
        eventid: this.$route.params.id
      }
    }).then(response => {
      console.log("抓到銀髮大使活動資料", response.data);
      this.olderangelinfo = response.data;
      // this.test2 = response.data;
      // this.content = response.data[0][2];
      // this.$store.commit("setediteditolderangelinfo", response.data[0]);
    });
  },
  mounted() {
    // if (this.eventinfo[16] == "是") {
    //     this.getolderangeleventinfo();
    //   }
    this.$store.commit("sethostid", this.eventinfo[17]);
    this.$store.commit("setsignupnumber", 0);
  },
  components: {
    Header,
    fillbtn,
    Sidemenu,
    Footer,
    alertblock
  },
  computed: {
    showsidemenu() {
      return this.$store.state.showsidemenu;
    },
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
    hostid() {
      return this.$store.state.hostid;
    },
    maincolor() {
      return this.$store.state.maincolor;
    },
    userdata() {
      return this.$store.state.userprofiledata;
    },
    showalert() {
      return this.$store.state.alertshow;
    }
  },
  methods: {
    tosignuppage() {
      if (this.hassignup == true) {
        this.$store.dispatch("showalertani", "show");
        setTimeout(() => {
          this.$store.dispatch("showalertani", "close");
        }, 2000);
      } else {
        this.$router.push({
          name: "Signup",
          params: {
            id: this.$route.params.id
          }
        });
      }
    },
    alertmaxnumber() {
      alert("已額滿");
    },
    alertisdeadline() {
      alert("此活動已報名截止");
    },
    convertdate(date) {
      let str = date;
      // console.log(str)
      let strlist = str.split("-");
      let day = parseInt(strlist[2], 10) + 1 + "";
      let result = (strlist[0] + "/" + strlist[1] + "/" + day).substring(0, 10);

      return result;
    }
  }
};
</script>

